import { useState } from "react";
import { User } from "../interfaces/User";
import axios from "axios";
type LoginParams = {
	email: string | undefined;
	password: string | undefined;
};

export const useLogin = () => {
	const EMAIL = process.env.EMAIL;
	const PASSWORD = process.env.PASSWORD;
	const [user, setUser] = useState<User | null>(null);
	const [error, setError] = useState<string | null>(null);
	const login = async (loginParams: LoginParams): Promise<User> => {
		const { email, password } = loginParams;
		const LoginURL = "https://admindev.inceptia.ai/api/v1/login/";
		const userInfo = { email: email, password: password };
		const config = {
			headers: {
				"Content-Type": "application/json",
			},
		};
		const response = await axios.post(
			LoginURL,
			{ email: "reactdev@iniceptia.ai", password: "4eSBbHqiCTPdBCTj" },
			config
		);
		if (!response.data) {
			throw new Error("Failed to login");
		}

		const data: User = await response.data.json();
		return data;
	};

	const loginUser = async () => {
		try {
			const userData = await login({ email: EMAIL, password: PASSWORD });
			setUser(userData);
			setError(null);
		} catch (err) {
			setError("ERROR - LOGIN - POST: Failed to login");
			setUser(null);
		}
	};
	const logoutUser = () => {
		setUser(null);
		setError(null);
	};
	return {
		EMAIL,
		PASSWORD,
		user,
		error,
		loginUser,
		logoutUser,
	};
};
